</template>

<script>
import SidebarComp from '../Sidebar.vue'
import trainingImg from '../../../assets/images/training-3.jpg'
export default {
    data(){
        return{
            training:{
                id:3,
                    title:'Brand Life Train the Trainer',
                    img:trainingImg,
                    url:'/trainings/brand-life-train-the-trainer',
                    content:`
                        <p>We believe that trainers must be equipped with the knowledge and skills needed to convey information in a compelling manner. BL train the trainer, learn how to effectively train in today's corporate environment. Our trainers share strategies and techniques for delivering high-quality workplace training that lead to better learner outcomes.</p>
                        <p>We cover developing, planning, and delivering successful training sessions, as well as how to strengthen your communication skills and overcome learning barriers. In addition to preparing you to deliver training sessions, the skills covered in this course can make you more adept at public speaking, coaching individuals, and leading a team.</p>
                        <h4>Training innovation:</h4>
                        <ul>
                            <li>Product  and Service based Trainings</li>
                            <li>Human Capital Development based Training</li>
                        </ul>
                    `
            }
        }
    },
    components:{
        SidebarComp
    }
}
</script>

