<template>
    <div id="about">
        
        <section>
            <div class="page-header">
                <div class="page-header-overlay"></div>
                <h2 class="text-center">{{sectionTitle}}</h2>
                <div class="slash w-15 my-2 mx-auto"></div>
            </div>
            <div class="page-grid my-5">
                <div class="container">
                    <div class="row">
                        <div class="col-md-8 mb-5 order-md-2 order-1" v-if="searchPosts.length > 0 && search !== ''">
                            <div class="row">
                                <div class="col-md-12" v-for="(post,index) in searchPosts" :key="index" >
                                    <div class="card">
                                        <img :src="post.img" alt="" class="card-img-top">
                                        <div class="card-body">
                                            <h2 class="my-3"><router-link :to="post.url">{{post.title}}</router-link></h2>
                                            <div class="d-flex align-items-center pb-2 category">
                                                <small>Category: <span>{{post.category}}</span></small>
                                            </div>
                                            <div v-html="post.content"></div>
                                            <router-link :to="post.url" class="btn btn-dark">Read More <i class="fa fa-angle-right"></i></router-link>
                                        </div>
                                    </div>
                                    <hr class="my-5">
                                </div>
                            </div>
                        </div>
                        <div class="col-md-8 mb-5 order-md-2 order-1" v-else>
                            <div class="row">
                                <div class="col-md-12">
                                    <h4 class="text-center">
                                        Sorry No posts found for: {{search}}
                                    </h4>
                                </div>
                            </div>
                        </div>
                        <div class="col-md-4 order-md-1 order-2">
                            <sidebar-comp @updateQuery="search = $event"/>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>


<script>
import SidebarComp from './templates/Sidebar.vue'

import {bus} from '../main.js'

import training1Img from '../assets/images/training-1.jpg'
import training2Img from '../assets/images/training-2.jpg'
import training3Img from '../assets/images/training-3.jpg'

import activation1Img from '../assets/images/activation-1.jpg'
import activation2Img from '../assets/images/activation-2.jpg'
import activation3Img from '../assets/images/activation-3.jpg'

export default {
    data(){
        return{
            sectionTitle:'Search Result',
            search:'',
            searchPosts:[],
            posts:[
                {
                    id:1,
                    title:'Brand Life OBT',
                    img:training1Img,
                    category:'Training',
                    url:'/trainings/brand-life-obt',
                    content:`
                        <p>Brand Life Outbound Training - Outbound Learning is a training method for enhancing individual, team and organizational performance through experiential learning methodology where participants are involved into outbound training activities and outbound training games that give a immersive learning experience and bring out variety of learning outcomes based on the participants and organizational learning needs......</p>
                    `
                },
                {
                    id:2,
                    title:'Brand Life Classroom',
                    img:training2Img,
                    category:'Training',
                    url:'/trainings/brand-life-classroom',
                    content:`
                        <p>We, Brand Life offer an exciting range of Indoor Training Programmes that enhance support to achieved Great place to work recognition. We do this by continually advancing the development of people for the benefit of the individual, employee and the organization at large.....</p>
                    `
                },
                {
                    id:3,
                    title:'Brand Life Train the Trainer',
                    img:training3Img,
                    category:'Training',
                    url:'/trainings/brand-life-train-the-trainer',
                    content:`
                        <p>We believe that trainers must be equipped with the knowledge and skills needed to convey information in a compelling manner. BL train the trainer, learn how to effectively train in today's corporate environment. Our trainers share strategies and techniques for delivering high-quality workplace training that lead to better learner outcomes....</p>
                    `
                },
                {
                    id:4,
                    title:'Brand Life Activations',
                    img:activation1Img,
                    category:'Activation',
                    url:'/activations/brand-life-activations',
                    content:`
                        <p>It is a team of young, creative, enthusiastic and dynamic professionals with a sparkling stream of ideas. We have a vast experience in event management & brand activation and conduct events at a national level....</p>
                    `
                },
                {
                    id:5,
                    title:'Brand Life Engagement',
                    img:activation2Img,
                    category:'Activation',
                    url:'/activations/brand-life-engagement',
                    content:`
                        <p>Through integrated engagement methodologies, BL seamlessly blends digital, mobile, gaming, social media and other technology-based modes of engagement with real-life experiences to deliver impressive ROI....</p>
                    `
                },
                {
                    id:6,
                    title:'Brand Life Events',
                    img:activation3Img,
                    category:'Activation',
                    url:'/activations/brand-life-events',
                    content:`
                        <p>Our events conceptualize, strategize, plan and design to become exciting and animated experience through Total event management solution – from strategy to execution.....</p>
                    `
                }
            ]
        }
    },
    components:{
        SidebarComp
    },
    watch: {
        search(value) {
            this.search = value;
            this.searchPosts = []
            this.showPosts()
        }
    },
    mounted(){
        this.search = this.$route.query.s
        bus.$on('updateQuery', (value) => {
            this.search = value
        })
        this.showPosts();
    },
    methods:{
        showPosts(){
            for(var i in this.posts){
                if(this.posts[i].title.toUpperCase().indexOf(this.search.toUpperCase()) > -1){
                    this.searchPosts.push(this.posts[i])
                }
            }
        }
    },
    
}
</script>

<style lang="scss" scoped>
    $secondary-clr:#dd9933;

    .category span{
        color: $secondary-clr;
    }
    
</style>


