<template>
    <section>
            <div class="page-header">
                <div class="page-header-overlay"></div>
                <h2 class="text-center">{{sectionTitle}}</h2>
                <div class="slash w-15 my-2 mx-auto"></div>
            </div>

            <div class="page-grid my-5">
                <div class="container">
                    <div class="row">
                        <div class="col-md-8 order-md-2 order-1">
                            <div class="row">
                                <div class="col-md-12" v-for="training in trainings" :key="training.id">
                                    <div class="card">
                                        <img :src="training.img" alt="" class="card-img-top">
                                        <div class="card-body">
                                            <h2 class="my-3"><router-link :to="training.url">{{training.title}}</router-link></h2>
                                            <div v-html="training.content"></div>
                                            <router-link :to="training.url" class="btn btn-dark">Read More <i class="fa fa-angle-right"></i></router-link>
                                        </div>
                                    </div>
                                    <hr class="my-5">
                                </div>
                            </div>
                        </div>
                        <div class="col-md-4 order-md-1 order-2">
                            <sidebar-comp />
                        </div>
                    </div>
                </div>
            </div>
        </section>
</template>


<script>
import SidebarComp from '../Sidebar.vue'
import training1Img from '../../../assets/images/training-1.jpg'
import training2Img from '../../../assets/images/training-2.jpg'
import training3Img from '../../../assets/images/training-3.jpg'
export default {
    data(){
        return{
            sectionTitle:'WE INTRODUCE TO YOU OUR TRAININGS',
            sectionDesc:'Brand Life offer trainings in the following fields',
            trainings:[
                {
                    id:1,
                    title:'Brand Life OBT',
                    img:training1Img,
                    url:'/trainings/brand-life-obt',
                    content:`
                        <p>Brand Life Outbound Training - Outbound Learning is a training method for enhancing individual, team and organizational performance through experiential learning methodology where participants are involved into outbound training activities and outbound training games that give a immersive learning experience and bring out variety of learning outcomes based on the participants and organizational learning needs......</p>
                    `
                },
                {
                    id:2,
                    title:'Brand Life Classroom',
                    img:training2Img,
                    url:'/trainings/brand-life-classroom',
                    content:`
                        <p>We, Brand Life offer an exciting range of Indoor Training Programmes that enhance support to achieved Great place to work recognition. We do this by continually advancing the development of people for the benefit of the individual, employee and the organization at large.....</p>
                    `
                },
                {
                    id:3,
                    title:'Brand Life Train the Trainer',
                    img:training3Img,
                    url:'/trainings/brand-life-train-the-trainer',
                    content:`
                        <p>We believe that trainers must be equipped with the knowledge and skills needed to convey information in a compelling manner. BL train the trainer, learn how to effectively train in today's corporate environment. Our trainers share strategies and techniques for delivering high-quality workplace training that lead to better learner outcomes....</p>
                    `
                }
            ],
        }
    },
    components:{
        SidebarComp
    }
}
</script>

<style lang="scss">
    $secondary-clr:#dd9933;
    $primary-clr:#2c3e50;
    .slash{
        width: 150px;
        height: 3px;
        background: $secondary-clr;   
    }
    .card{
        border: none;

        h2{
            a{
                color: $primary-clr;

                &:hover{
                    color: $secondary-clr;
                    text-decoration: none;
                }
            }
        }
    }

    .page-header{
        position: relative;
        background: url('../../../assets/images/features-bg.jpg') no-repeat center center;
        height: 350px;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        padding-top: 100px;
        color: white;

        .page-header-overlay{
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            background: rgba($secondary-clr, 0.1);
        }
    }
    
</style>



