<template>
    <div id="training">
        <section>
            <div class="activation-heading bg-light py-5">
                <div class="container">
                    <div class="row">
                        <div class="col-md-10 mx-auto text-center" v-scroll-reveal="{origin:'bottom'}">
                            <h2>{{sectionTitle}}</h2>
                            <div class="slash my-3 mx-auto"></div>
                            <p>{{sectionDesc}}</p>
                        </div>
                    </div>
                </div>
            </div> 
            <div class="front-activation my-5">
                <div class="container">
                    <div class="row">
                        <div v-for="activation in activations" :key="activation.id" class="col-md-4 mb-3">
                            <div class="activation-container" v-scroll-reveal="activation.animationClass">
                                <div>
                                    <img :src="activation.img" class="img-fluid activation-image" alt="">
                                </div>
                                <h5 class="activation-title my-4"><router-link :to="activation.url">{{activation.title}}</router-link></h5>
                                <p class="activation-desc" v-html="activation.content"></p>
                                <router-link :to="activation.url" class="btn btn-dark btn-sm">Read More <i class="fa fa-angle-right"></i></router-link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
import activation1Img from '../../assets/images/activation-1.jpg'
import activation2Img from '../../assets/images/activation-2.jpg'
import activation3Img from '../../assets/images/activation-3.jpg'
export default {
    data(){
        return{
            sectionTitle:'Brand Life Activation',
            sectionDesc:'',
            activations:[
                {
                    id:1,
                    title:'Brand Life Activations',
                    img:activation1Img,
                    animationClass:{origin:'left', distance: '150px'},
                    url:'/activations/brand-life-activations',
                    content:`
                        <p>It is a team of young, creative, enthusiastic and dynamic professionals with a sparkling stream of ideas. We have a vast experience in event management & brand activation and conduct events at a national level.</p>
                    `
                },
                {
                    id:2,
                    title:'Brand Life Engagement',
                    img:activation2Img,
                    animationClass:{origin:'bottom', distance: '150px'},
                    url:'/activations/brand-life-engagement',
                    content:`
                        <p>Through integrated engagement methodologies, BL seamlessly blends digital, mobile, gaming, social media and other technology-based modes of engagement with real-life experiences to deliver impressive ROI.</p>
                    `
                },
                {
                    id:3,
                    title:'Brand Life Events',
                    img:activation3Img,
                    animationClass:{origin:'right', distance: '150px'},
                    url:'/activations/brand-life-events',
                    content:`
                        <p>Our events conceptualize, strategize, plan and design to become exciting and animated experience through Total event management solution – from strategy to execution.</p>
                    `
                }
            ],
        }
    },
    mounted(){
        
    }
}
</script>


<style lang="scss">
    $primary-clr:#2c3e50;
    $secondary-clr:#dd9933;
    .activation-container {
        position: relative;
        width: 100%;

        &:hover{
            .activation-image {
                transform: scale(1.1);
            }
            .activation-middle {
                opacity: 1;
            }
        }

        .activation-image {
            opacity: 1;
            display: block;
            width: 100%;
            height: auto;
            transition: .5s ease;
            backface-visibility: hidden;
        }
        .activation-middle {
            transition: .5s ease;
            opacity: 0;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
            text-align: center;
        }
        .activation-text {
            color: white;
            font-size: 16px;
            padding: 16px 32px;
        }

        .activation-title{
            a{
                color: $primary-clr;
                &:hover{
                    color: $secondary-clr;
                    text-decoration: none;
                }
            }
        }
    }
</style>


