<template>
    <div id="training1">
        <section>
            <div class="page-header">
                <div class="page-header-overlay"></div>
                <h2 class="text-center">{{training.title}}</h2>
                <div class="slash w-15 my-2 mx-auto"></div>
            </div>

            <div class="page-grid my-5">
                <div class="container">
                    <div class="row">
                        <div class="col-md-8 order-md-2 order-1">
                            <div class="row">
                                <div class="col-md-12">
                                    <div class="card">
                                        <img :src="training.img" alt="" class="card-img-top">
                                        <div class="card-body">
                                            <h2 class="my-3">{{training.title}}</h2>
                                            <div v-html="training.content"></div>
                                        </div>
                                    </div>
                                    <hr class="my-5">
                                </div>
                            </div>
                        </div>
                        <div class="col-md-4 order-md-1 order-2">
                            <sidebar-comp />
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
import SidebarComp from '../Sidebar.vue'
import trainingImg from '../../../assets/images/training-2.jpg'
export default {
    data(){
        return{
            training:{
                id:2,
                    title:'Brand Life Classroom',
                    img:trainingImg,
                    url:'/trainings/brand-life-classroom',
                    content:`
                        <p>We, Brand Life offer an exciting range of Indoor Training Programmes that enhance support to achieved Great place to work recognition. We do this by continually advancing the development of people for the benefit of the individual, employee and the organization at large.</p>
                        <p>We are geared to conduct a pre & post training tracking system to enable you to understand the current strengths and weaknesses of the target audience and the change experienced by the same audience due to the training.</p>
                        <p>The company is proud to have a team of very experienced, industry – backed, practicing Managers as Resource Persons from whom expertise is drawn depending upon the type of training requested.</p>
                        <h4>Training innovation:</h4>
                        <ul>
                            <li>Sales, After Sales, Customer Care Based Programs.</li>
                            <li>Grooming and Dinning Etiquette Programs.</li>
                            <li>Presentation and Communication Skills Development Programs.</li>
                            <li>NLP Based Industrial & Organizational Corporate Training Programs.</li>
                        </ul>
                    `
            }
        }
    },
    components:{
        SidebarComp
    }
}
</script>

