<template>
    <div id="showcase">
        <section>
            <div class="wrap">
              <!-- <div id="arrow-left" class="arrow"></div> -->
              <!-- <div id="left-arrow" @click="slideLeft" class="arrow">
                <i class="fa fa-angle-left"></i>
              </div> -->
             <div id="slider">
                
               <div class="slide slide1 animated fadeIn">
                   <div class="slider-overlay"></div>
                 <div class="slide-content">
                   <div class="container">
                       <div class="row mt-5 d-flex align-items-center">
                           <div class="col-md-8 text-white text-center mx-auto">
                               <h1 class="slider-header animated lightSpeedIn">Developing Strong Leaders</h1>
                               <div class="slash my-3 mx-auto"></div>
                               <div class="slider-text animated fadeInLeft">Unleash the leader in you!</div>
                               <div class="slider-text animated fadeInLeft mb-2">Here is how we can help you</div>
                               <router-link to="/trainings" class="slider-btn btn btn-primary mr-3 animated bounceIn">Learn More</router-link>
                           </div>
                       </div>
                   </div>
                 </div>
               </div>
               <div class="slide slide2 animated fadeIn">
                   <div class="slider-overlay"></div>
                 <div class="slide-content">
                   <div class="container">
                       <div class="row d-flex align-items-center mt-5">
                           <div class="col-md-8 mx-auto text-white text-center">
                           <h1 class="slider-header animated lightSpeedIn">We are specialized in engaging people & creating experiences</h1>
                           <div class="slash my-3 mx-auto"></div>
                           <p class="slider-text animated fadeInLeft">Bring your brand to life with 360 Activation from Strategy to Execution</p>
                               <router-link to="/activations" class="slider-btn btn btn-primary animated bounceIn">Learn More</router-link>
                           </div>
                       </div>
                   </div>
                 </div>
               </div>
               
             </div>
             <!-- <div id="arrow-right" class="arrow"></div> -->
             <!-- <div id="right-arrow" @click="slideRight" class="arrow">
                <i class="fa fa-angle-right"></i>
              </div> -->
           </div>
          </section>
    </div>
</template>

<script>
import slider1_img from '../../assets/images/slider-3.jpg';
import slider2_img from '../../assets/images/slider-2.jpg';
export default {
    data(){
        return{
            slider1_img,
            slider2_img,
            current:0,
            sliderImages:[],
            red:'color'
        }
    },
    mounted() {
        this.sliderImages = document.querySelectorAll(".slide");
            
        // Init slider
        this.startSlide();
        setInterval(this.slides, 10000)
    },
    methods: {
        // Show prev
        slideLeft() {
            this.reset();
            this.current--;
            if(this.current < 0){
                this.current = this.sliderImages.length - 1;
            }
            this.sliderImages[this.current].style.display = "block";
        },
        // Show next
        slideRight() {
            this.reset();
            this.current++;
            if(this.current == this.sliderImages.length){
                this.current = 0;
            }
            this.sliderImages[this.current].style.display = "block";
        },

        // Start slide at mount function
        slides() {
            if (this.current === this.sliderImages.length - 1) {
            this.current = -1;
            }
            this.slideRight();
        },

        // Start slide function
        startSlide() {
            this.reset();
            this.sliderImages[0].style.display = "block";
        },

        // Clear all images
        reset () {
            for (let i = 0; i < this.sliderImages.length; i++) {
                this.sliderImages[i].style.display = "none";
            }
        }

}

}
</script>




<style lang="scss" scoped>

    $primary-clr:#000;
    $blue-clr:blue;
    $secondary-clr:#dd9933;
    $yellow-clr:#dd9933;

    .slash{
        width: 150px;
        height: 3px;
        background: $secondary-clr;   
    }
    #showcase{
    position: relative;
    height: 100vh;
    overflow-x: hidden;
    color: $primary-clr;

    .slider-overlay{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba($primary-clr, 0.5);
    }



    #slider,
    .wrap,
    .slide-content {
        margin: 0;
        padding: 0;
        font-family: Arial, Helvetica, sans-serif;
        width: 100%;
        height: 100vh;
        overflow-x: hidden;
        overflow: hidden;
        color: white;

        &::before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            //background: rgba($primary-clr,  0.3);
        }
    }

    .wrap {
        position: relative;
    }

    .slide {
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        animation-duration: 1s;
    }

    .slide1{
        background-image: url('../../assets/images/slider-1.jpg');
    }

    .slide2{
        background-image: url('../../assets/images/slider-2.jpg');
    }

    .slider-header{
        animation-delay: 1s;
        animation-duration: 1s;
    }
    .slider-text{
        animation-delay: 1.5s;
        animation-duration: 1s;
    }

    .btn{
        animation-delay: 1.3s;
        animation-duration: 1s;
    }



    .slide-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        color: $primary-clr;
    img{
        animation-delay: 1.1s;
        animation-duration: 1s;
    }
    }

     

    .arrow {
    cursor: pointer;
        position: absolute;
        top: 50%;
        width: 30px;
        background: transparent;
        border: 2px solid rgba($primary-clr, 0.2);
        border-radius: 50%;
        text-align: center;
        color: rgba($primary-clr, 0.2);
        font-size: 1.2rem;
        z-index: 33;
    
    &:hover{
        background: $blue-clr;
        color: white;
        border: 2px solid rgba($blue-clr, 0.2);
    }
    }

    #left-arrow {
        left: 10px;
    }

    #right-arrow {
        right: 10px;
    }


    

    .slider-col-1{
        margin-left: 100px;
    }

    .slider-btns{
        position: absolute;
        top: 50%;
        right: 10px;
    }

    .slider-btn-1,.slider-btn-2{
        padding: 3px;
        margin: 10px;
        cursor: pointer;
    }
}

</style>


