<template>
    <div id="strength">
        <section>
            <div class="row py-5 bg-light">
                <div class="col-md-10 mx-auto" v-scroll-reveal="{origin:'bottom'}">
                    <h2 class="text-center pt-2">Our Strength</h2>
                    <div class="slash mx-auto my-4"></div>
                </div>
            </div>
            <div class="wrap">
              <!-- <div id="arrow-left" class="arrow"></div> -->
              <!-- <div id="left-arrow" @click="slideLeft" class="arrow">
                <i class="fa fa-angle-left"></i>
              </div> -->
             <div id="slider strength-slider">
               <div class="strength-slide slide1 animated slideInUp">
                 <div class="slide-content">
                   <div class="container">
                       <div class="row d-flex align-items-center">
                           <div class="col-md-12">
                               <div class="strength-grid d-flex align-items-center">
                                   <div class="strength-img mx-2">
                                       <img :src="testimonial1pic" class="img-fluid" alt="">
                                   </div>
                                   <div class="strength-img mx-2">
                                       <img :src="testimonial2pic" class="img-fluid" alt="">
                                   </div>
                                   <div class="strength-img mx-2">
                                       <img :src="testimonial3pic" class="img-fluid" alt="">
                                   </div>
                                   <div class="strength-img mx-2">
                                       <img :src="testimonial4pic" class="img-fluid" alt="">
                                   </div>
                                   <div class="strength-img mx-2">
                                       <img :src="testimonial5pic" class="img-fluid" alt="">
                                   </div>
                               </div>
                           </div>
                       </div>
                   </div>
                 </div>
               </div>
               <div class="strength-slide slide2 animated slideInUp">
                 <div class="slide-content">
                   <div class="container">
                       <div class="row d-flex align-items-center">
                           <div class="col-md-12 mx-auto">
                               <div class="strength-grid d-flex align-items-center">
                                   <div class="strength-img mx-2">
                                       <img :src="testimonial6pic" class="img-fluid" alt="">
                                   </div>
                                   <div class="strength-img mx-2">
                                       <img :src="testimonial7pic" class="img-fluid" alt="">
                                   </div>
                                   <div class="strength-img mx-2">
                                       <img :src="testimonial8pic" class="img-fluid" alt="">
                                   </div>
                                   <div class="strength-img mx-2 w-75">
                                       <img :src="testimonial9pic" class="img-fluid" alt="">
                                   </div>
                               </div>
                           </div>
                       </div>
                   </div>
                 </div>
               </div>
               
             </div>
             <!-- <div id="arrow-right" class="arrow"></div> -->
             <!-- <div id="right-arrow" @click="slideRight" class="arrow">
                <i class="fa fa-angle-right"></i>
              </div> -->
           </div>
        </section>
    </div>
</template>


<script>
import testimonial1pic from '../../assets/images/testimonial-1.png' 
import testimonial2pic from '../../assets/images/testimonial-2.png' 
import testimonial3pic from '../../assets/images/testimonial-3.png' 
import testimonial4pic from '../../assets/images/testimonial-4.png' 
import testimonial5pic from '../../assets/images/testimonial-5.png' 
import testimonial6pic from '../../assets/images/testimonial-6.png' 
import testimonial7pic from '../../assets/images/testimonial-7.png' 
import testimonial8pic from '../../assets/images/testimonial-8.png' 
import testimonial9pic from '../../assets/images/testimonial-9.png' 
export default {
    data(){
        return{
            sectionTitle:'Our Strength',
            current:0,
            sliderImages:[],
            red:'color',
            testimonial1pic,
            testimonial2pic,
            testimonial3pic,
            testimonial4pic,
            testimonial5pic,
            testimonial6pic,
            testimonial7pic,
            testimonial8pic,
            testimonial9pic,
        }
    },
    mounted() {
        this.sliderImages = document.querySelectorAll(".strength-slide");
            
        // Init slider
        this.startSlide();
        setInterval(this.slides, 6000)

        
    },
    methods: {
        // Show prev
        slideLeft() {
            this.reset();
            this.current--;
            if(this.current < 0){
                this.current = this.sliderImages.length - 1;
            }
            this.sliderImages[this.current].style.display = "block";
        },
        // Show next
        slideRight() {
            this.reset();
            this.current++;
            if(this.current == this.sliderImages.length){
                this.current = 0;
            }
            this.sliderImages[this.current].style.display = "block";
        },

        // Start slide at mount function
        slides() {
            if (this.current === this.sliderImages.length - 1) {
            this.current = -1;
            }
            this.slideRight();
        },

        // Start slide function
        startSlide() {
            this.reset();
            this.sliderImages[0].style.display = "block";
        },

        // Clear all images
        reset () {
            for (let i = 0; i < this.sliderImages.length; i++) {
                this.sliderImages[i].style.display = "none";
            }
        }

}

}
</script>


<style lang="scss" scoped>

    $blue-clr:blue;
    $secondary-clr:#dd9933;
    $yellow-clr:#dd9933;

    .slash{
        width: 150px;
        height: 3px;
        background: $secondary-clr;   
    }
    .strength-grid{
        height: 200px;
        margin: 50px 0;
    }
    #strength{
    position: relative;
    overflow-x: hidden;

    .strength-slider{
        height: 400px;
    }


    #slider,
    .wrap,
    .slide-content {
        margin: 0;
        padding: 0;
        font-family: Arial, Helvetica, sans-serif;
        width: 100%;
        
        overflow-x: hidden;
        overflow: hidden;
        color: white;

        &::before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            //background: rgba($primary-clr,  0.3);
        }
    }

    .wrap {
        position: relative;
    }

    .strength-slide {
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        animation-duration: 1s;
    }

    // .slide1{
    //     background-image: url('../../assets/images/slider-1.jpg');
    // }

    // .slide2{
    //     background-image: url('../../assets/images/slider-2.jpg');
    // }

    .slider-header{
        animation-delay: 1s;
        animation-duration: 1s;
    }
    .slider-text{
        animation-delay: 1.5s;
        animation-duration: 1s;
    }

    .btn{
        animation-delay: 1.3s;
        animation-duration: 1s;
    }
    span{
        animation-delay: 1s;
        animation-duration: 1s;
    }



    .slide-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        
    img{
        animation-delay: 1.1s;
        animation-duration: 1s;
    }
    }

     

    .arrow {
    cursor: pointer;
        position: absolute;
        top: 50%;
        width: 30px;
        background: transparent;
        
        border-radius: 50%;
        text-align: center;
        font-size: 1.2rem;
        z-index: 33;
    
    &:hover{
        background: $blue-clr;
        color: white;
        border: 2px solid rgba($blue-clr, 0.2);
    }
    }

    #left-arrow {
        left: 10px;
    }

    #right-arrow {
        right: 10px;
    }


    

    .slider-col-1{
        margin-left: 100px;
    }

    .slider-btns{
        position: absolute;
        top: 50%;
        right: 10px;
    }

    .slider-btn-1,.slider-btn-2{
        padding: 3px;
        margin: 10px;
        cursor: pointer;
    }
}


</style>



