<template>
    <div id="header">
        <!-- Scroll to top btn -->
        <button @click="topFunction" id="myBtn" title="Go to top"><i class="fa fa-angle-up"></i></button>
        <header>
            <nav class="navbar navbar-expand-lg fixed-top bg-white navbar-light">
              <div class="container">
                <a class="navbar-brand" href="/"><img :src="logo" class="img-fluid brand-img" alt=""></a>
                <i class="fa fa-bars fa-2x navbar-btn d-lg-none d-sm-block" @click="showSideNavbar = !showSideNavbar"></i>
                
                <div class="collapse navbar-collapse" id="navbarNav">
                    <ul class="navbar-nav ml-auto">
                    <li class="nav-item">
                        <router-link class="nav-link" to="/">HOME</router-link>
                    </li>
                    <li class="nav-item">
                        <router-link class="nav-link" to="/about">ABOUT</router-link>
                    </li>
                    <li class="nav-item">
                        <router-link class="nav-link" to="/trainings">TRAININGS</router-link>
                    </li>
                    <li class="nav-item">
                        <router-link class="nav-link" to="/activations">ACTIVATIONS</router-link>
                    </li>
                    <!-- <li class="nav-item">
                        <a class="nav-link" href="/Testimonial">Testimonial</a>
                    </li> -->
                    <li class="nav-item">
                      <router-link class="nav-link" to="/contact">CONTACT</router-link>
                    </li>
                    <li class="nav-item search-btn">
                      <a class="nav-link" @click="showSearchBar = !showSearchBar"><i class="fa fa-search"></i></a>

                      <transition name="search-box">
                        <div class="search py-3 px-2" v-if="showSearchBar">
                            <div class="menu-search">
                                <form @submit.prevent="submitSearchForm">
                                    <div class="form-group border m-0">
                                        <div class="input-group">
                                            <input type="search" v-model="s" name="s" placeholder="Search Here...." class="form-control border-0">
                                            <div class="input-group-prepend border-left">
                                                <div class=" bg-none">
                                                    <button type="submit" class="search-icon btn"><i class="fa fa-search"></i></button>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                      </transition>
                    </li>
                    </ul>
                </div>
              </div>
            </nav>

            

            <transition name="sidebar-box">
                <div class="sidebar" v-if="showSideNavbar">
                    <div class="menu-sidebar">
                        <div class="">
                            <i class="fa fa-times fa-2x navbar-close-btn" @click="showSideNavbar = !showSideNavbar"></i>
                        </div>
                        <div class="sidebar-icon text-center py-3">
                            <a href="/"><img :src="logo" class="img-fluid brand-img" alt=""></a>
                        </div>
                        <ul>
                            <li><router-link to="/">HOME</router-link></li>    
                            <li><router-link to="/about">ABOUT</router-link></li>    
                            <li><router-link to="/trainings">TRAININGS</router-link></li>    
                            <li><router-link to="/activations">ACTIVATIONS</router-link></li>    
                            <li><router-link to="/contact">CONTACT</router-link></li>    
                        </ul>   
                        <div class="menu-search w-75 mx-auto">
                            <form @submit.prevent="submitSearchForm">
                                <div class="form-group">
                                    <input type="search" name="s" v-model="s" placeholder="Search Here...." class="form-control">
                                </div>
                                <div class="form-group text-center">
                                    <button class="btn btn-success btn-block"><b>Search</b> &nbsp;<i class="fa fa-search"></i></button>
                                </div>
                            </form>
                        </div> 
                    </div>    
                </div>
            </transition>
        </header>
    </div>
</template>

<script>
import logo from '../../assets/images/brand life logo1.jpg'
import {bus} from '../../main.js'
export default {
    data(){
        return{
            s:'',
            logo,
            showSideNavbar:false,
            showSearchBar:false
        }
    },
    props:{
        msg:String
    },
    methods:{
        topFunction(){
            document.body.scrollTop = 0; // For Safari
            document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
        },
        scrollFunction(){
            if (document.body.scrollTop > 80 || document.documentElement.scrollTop > 80){
                document.querySelector('#myBtn').classList.add('d-block')
            }else{
                document.getElementById("myBtn").classList.remove('d-block')
            }
        },
        submitSearchForm(){
            this.$router.push({
                name:'search',
                query:{
                    s:this.s
                }
            })
            bus.$emit('updateQuery', this.s);
            this.showSearchBar = false;
            this.s = '',
            this.showSideNavbar = false;
        }
    },
    mounted(){
        window.onscroll = () => this.scrollFunction();
    }
}
</script>

<style lang="scss" scoped>
    $primary-clr:#2c3e50;
    $secondary-clr:#dd9933;
    #header{

        #myBtn {
            display: none; /* Hidden by default */
            position: fixed; /* Fixed/sticky position */
            bottom: 20px; /* Place the button at the bottom of the page */
            right: 30px; /* Place the button 30px from the right */
            z-index: 99; /* Make sure it does not overlap */
            border: none; /* Remove borders */
            outline: none; /* Remove outline */
            background-color: $secondary-clr; /* Set a background color */
            color: white; /* Text color */
            cursor: pointer; /* Add a mouse pointer on hover */
            padding: 7px 18px; /* Some padding */
            border-radius: 50%; /* Rounded corners */
            font-size: 22px; /* Increase font size */
            transition: all 0.5s ease-out;
        }
        
        #myBtn:hover {
            background-color: #555; /* Add a dark-grey background on hover */
            transition: all 0.5s ease-out;
        }

        nav{
            z-index: 10;
        }
        position: relative;
        .nav-link{
            margin:5px 20px;
            color: $primary-clr;

            &:hover{
                color:$secondary-clr;
                transition: all 0.5s ease-out;
            }
        }
        .brand-img{
            width: 140px;
        }
        .navbar-brand{
            padding: 0;
            margin: 0;
        }

        .router-link-exact-active{
            color:$secondary-clr;
        }

        .navbar-btn{
            cursor: pointer;
            &:hover{
                color: $secondary-clr;
            }
        }

        
    }
    .sidebar{
            position: fixed;
            top: 0;
            left: 0;
            width: 300px;
            height: 100%;
            background: white;
            z-index: 11;

            .menu-sidebar{
                position: relative;
            }

            .navbar-close-btn{
                cursor: pointer;
                position: absolute;
                top:20px;
                right: 20px;

                &:hover{
                    color: $secondary-clr;
                    padding: 0;
                    display: inline-block;
                }
            }

            ul{
               padding: 30px 10px;
                li{
                    list-style-type: none;
                    border-bottom: 1px solid $primary-clr;
                    text-align: center;
                    a{
                        color: $primary-clr;
                        padding: 10px;
                        display: block;
                        &:hover{
                            color: $secondary-clr;
                            text-decoration: none;
                        }
                    }
                }
            }
        }

    .search-btn{
            position: relative;
            
            a{
                cursor: pointer;
                &:hover{
                    color: $secondary-clr;
                    transition: all 0.5s ease-out;
                }
            }
            .search{
                position: absolute;
                right: 0;
                width: 300px;
                background: white;
                //padding: 10px;
                border-top: 3px solid $secondary-clr;

                .form-group{
                    border-radius: 5px;
                }
            }

            .search-icon{
                background: transparent;
                color: $secondary-clr;
            }
            
        }

            .router-link-exact-active{
                color: $secondary-clr;
        }

        .search-box-enter{
            transform: translateY(40px);
            opacity: 0;
        }

        .search-box-enter-active{
            transition: all 1s ease-out;
        }

        .search-box-leave{
            
        }

        .search-box-leave-active{
            opacity: 0;
            transform: translateY(30px);
            transition: all 1s;
        }



        .sidebar-box-enter{
            transform: translateX(-300px);
            opacity: 0;
        }

        .sidebar-box-enter-active{
            transition: all 1s ease-out;
        }

        .sidebar-box-leave{
            
        }

        .sidebar-box-leave-active{
            opacity: 0;
            transform: translateX(-300px);
            transition: all 1s;
        }


</style>

