<template>
    <div id="training1">
        <section>
            <div class="page-header">
                <div class="page-header-overlay"></div>
                <h2 class="text-center">{{training.title}}</h2>
                <div class="slash w-15 my-2 mx-auto"></div>
            </div>

            <div class="page-grid my-5">
                <div class="container">
                    <div class="row">
                        <div class="col-md-8 order-md-2 order-1">
                            <div class="row">
                                <div class="col-md-12">
                                    <div class="card">
                                        <img :src="training.img" alt="" class="card-img-top">
                                        <div class="card-body">
                                            <h2 class="my-3">{{training.title}}</h2>
                                            <div v-html="training.content"></div>
                                        </div>
                                    </div>
                                    <hr class="my-5">
                                </div>
                            </div>
                        </div>
                        <div class="col-md-4 order-md-1 order-2">
                            <sidebar-comp />
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
import SidebarComp from '../Sidebar.vue'
import trainingImg from '../../../assets/images/training-1.jpg'
export default {
    data(){
        return{
            training:{
                id:1,
                    title:'Brand Life OBT',
                    img:trainingImg,
                    content:`
                        <p>Brand Life Outbound Training - Outbound Learning is a training method for enhancing individual, team and organizational performance through experiential learning methodology where participants are involved into outbound training activities and outbound training games that give a immersive learning experience and bring out variety of learning outcomes based on the participants and organizational learning needs.</p>
                        <p>Brand Life Outbound Training generally revolves around outbound activities designed to improve leadership, communication skills, planning, change management, delegation, teamwork, and motivation. Participants are divided into teams and assigned tasks or activities for completion in a specified time.</p>
                        <p>Brand Life is also  an Outbound Training company that strongly believes in delivering impactful outbound training programs that can be the key element of developing a tight knit, effective and high performance teams for your organization.</p>
                    `
            }
        }
    },
    components:{
        SidebarComp
    }
}
</script>

