<template>
    <div id="training">
        <section>
            <div class="training-heading bg-light py-5">
                <div class="container">
                    <div class="row">
                        <div class="col-md-10 mx-auto text-center" v-scroll-reveal="{origin:'bottom'}">
                            <h2>{{sectionTitle}}</h2>
                            <div class="slash my-3 mx-auto"></div>
                            <p>{{sectionDesc}}</p>
                        </div>
                    </div>
                </div>
            </div> 
            <div class="trainings my-5">
                <div class="container">
                    <div class="row">
                        <div v-for="training in trainings" :key="training.id" class="col-md-4 mb-3"
                        v-scroll-reveal="{origin:'bottom', distance:'0px', delay:1000}">
                            <div class="training-container">
                                <img :src="training.img" class="img-fluid training-img" alt="">
                                <div class="training-overlay">
                                    <router-link :to="training.url">
                                        <h2 class="training-text">{{training.title}}</h2>
                                    </router-link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="text-center">
                <router-link to="/trainings" class="btn btn-dark mb-4">Learn More</router-link>
            </div>
        </section>
    </div>
</template>

<script>
import training1Img from '../../assets/images/training-1.jpg'
import training2Img from '../../assets/images/training-2.jpg'
import training3Img from '../../assets/images/training-3.jpg'
export default {
    data(){
        return{
            sectionTitle:'Brand Life Training',
            sectionDesc:'Brand Life offer trainings in the following fields',
            trainings:[
                {
                    id:1,
                    title:'Brand Life OBT',
                    img:training1Img,
                    url:'/trainings/brand-life-obt',
                    content:`
                        <p>Brand Life Outbound Training - Outbound Learning is a training method for enhancing individual, team and organizational performance through experiential learning methodology where participants are involved into outbound training activities and outbound training games that give a immersive learning experience and bring out variety of learning outcomes based on the participants and organizational learning needs.</p>
                        <p>Brand Life Outbound Training generally revolves around outbound activities designed to improve leadership, communication skills, planning, change management, delegation, teamwork, and motivation. Participants are divided into teams and assigned tasks or activities for completion in a specified time.</p>
                        <p>Brand Life is also  an Outbound Training company that strongly believes in delivering impactful outbound training programs that can be the key element of developing a tight knit, effective and high performance teams for your organization.</p>
                    `
                },
                {
                    id:2,
                    title:'Brand Life Classroom',
                    img:training2Img,
                    url:'/trainings/brand-life-classroom',
                    content:`
                        <p>We, Brand Life offer an exciting range of Indoor Training Programmes that enhance support to achieved Great place to work recognition. We do this by continually advancing the development of people for the benefit of the individual, employee and the organization at large.</p>
                        <p>We are geared to conduct a pre & post training tracking system to enable you to understand the current strengths and weaknesses of the target audience and the change experienced by the same audience due to the training.</p>
                        <p>The company is proud to have a team of very experienced, industry – backed, practicing Managers as Resource Persons from whom expertise is drawn depending upon the type of training requested.</p>
                        <h4>Training innovation:</h4>
                        <ul>
                            <li>Sales, After Sales, Customer Care Based Programs.</li>
                            <li>Grooming and Dinning Etiquette Programs.</li>
                            <li>Presentation and Communication Skills Development Programs.</li>
                            <li>NLP Based Industrial & Organizational Corporate Training Programs.</li>
                        </ul>
                    `
                },
                {
                    id:3,
                    title:'Brand Life Train the Trainer',
                    img:training3Img,
                    url:'/trainings/brand-life-train-the-trainer',
                    content:`
                        <p>We believe that trainers must be equipped with the knowledge and skills needed to convey information in a compelling manner. BL train the trainer, learn how to effectively train in today's corporate environment. Our trainers share strategies and techniques for delivering high-quality workplace training that lead to better learner outcomes.</p>
                        <p>We cover developing, planning, and delivering successful training sessions, as well as how to strengthen your communication skills and overcome learning barriers. In addition to preparing you to deliver training sessions, the skills covered in this course can make you more adept at public speaking, coaching individuals, and leading a team.</p>
                        <h4>Training innovation:</h4>
                        <ul>
                            <li>Product  and Service based Trainings</li>
                            <li>Human Capital Development based Training</li>
                        </ul>
                    `
                }
            ],
        }
    },
    mounted(){
        
    }
}
</script>

<style lang="scss">
    $secondary-clr:#dd9933;
    .training-container {
        position: relative;
        width: 100%;

        &:hover{
            .training-overlay{
                height: 100%;
            }
        }

        .training-image {
            display: block;
            width: 100%;
            height: auto;
        }

        .training-overlay {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            background-color: white;
            overflow: hidden;
            width: 100%;
            height: 0;
            transition: .5s ease;
            opacity: 0.7;

            .training-text {
                color: black;
                font-size: 23px;
                position: absolute;
                font-weight: 500;
                top: 50%;
                left: 50%;
                -webkit-transform: translate(-50%, -50%);
                -ms-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
                text-align: center;
                opacity: 1;

                &:hover{
                    color: $secondary-clr;
                }
            }
        }
    }
</style>


