<template>
    <div id="contact">
        <section>
            <div class="page-header">
                <div class="page-header-overlay"></div>
                <h2 class="text-center">{{sectionTitle}}</h2>
                <div class="slash w-15 my-2 mx-auto"></div>
            </div>
            <GmapMap
                :center="{lat:6.8759, lng:79.9392}"
                :zoom="14"
                map-type-id="terrain"
                style=" height: 500px"
                >
                <GmapMarker
                    :key="index"
                    v-for="(m, index) in markers"
                    :position="m.position"
                    :clickable="true"
                    :draggable="true"
                    @click="center=m.position"
                />
            </GmapMap>

            <div class="contact-form bg-light py-5">
                <div class="container py-5">
                    <div class="row">
                        <div class="col-md-5">
                            <h2>Keep In Touch With Us</h2>
                            <p>Keep In Touch with us and get information about our services. And you can also send us a message.</p>
                            <ul>
                                <li class="d-flex align-items-center">
                                    <div class="mr-2"><i class="fa fa-user"></i></div>
                                    <div>Sanesh Fernando , Managing Director</div>
                                </li>
                                <li class="d-flex align-items-center">
                                    <div class="mr-2"><i class="fa fa-phone"></i></div>
                                    <div>+9477 028 46 70</div>
                                </li>
                                <li class="d-flex align-items-center">
                                    <div class="mr-2"><i class="fa fa-envelope"></i></div>
                                    <div>teamlead@brandlifeasia.com</div>
                                </li>
                                <li class="d-flex align-items-center">
                                    <div class="mr-2"><i class="fa fa-map-marker"></i></div>
                                    <div> 1122/1, Thalawathugoda, Sri Lanka.</div>
                                </li>
                                <div class="d-flex align-items-center mt-4">
                                    <div class="mr-3"><a href="https://www.facebook.com/brandlifeasia/"><i class="fab fa-facebook-f"></i></a></div>
                                    <div class="mr-3"><a href="https://www.twitter.com/brandlifeasia/"><i class="fab fa-twitter"></i></a></div>
                                    <div class="mr-3"><a href="https://www.linkedin.com/brandlifeasia/"><i class="fab fa-linkedin"></i></a></div>
                                </div>
                            </ul>
                        </div>
                        <div class="col-md-7">
                            <form action="#">
                                <div class="form-group">
                                    <label for="name">Name</label>
                                    <input type="text" placeholder="Enter Full Name" class="form-control">
                                </div>
                                <div class="form-group">
                                    <label for="email">Email</label>
                                    <input type="email" placeholder="Enter Email" class="form-control">
                                </div>
                                <div class="form-group">
                                    <label for="message">Message</label>
                                    <textarea placeholder="Enter Message" class="form-control" rows="3"></textarea>
                                </div>
                                <div class="form-group">
                                    <a href="#" class="btn btn-dark">Send Message</a>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
export default {
    data(){
        return{
            sectionTitle:'Contact us',
            markers:[
                {
                    position:{lat:6.8759, lng:79.9392},
                }
            ]
        }
    },
}
</script>


<style lang="scss">
    $secondary-clr:#dd9933;
    $primary-clr:#2c3e50;
    
        .contact-form{
            ul{
          padding: 0;
          li , div{
            list-style-type: none;
            line-height: 30px;

            &:hover{
              .fa{
                color: $secondary-clr;
              }
            }

            .fa , .fab{
              width: 20px;
            }

            a{
              color: $primary-clr;
              text-decoration: none;

              &:hover{
                color: $secondary-clr;
                text-decoration: none;
                transition: all 0.5s ease-out;
              }
            }
          }
        
    }
        }
</style>


