import IndexComp from './components/Index.vue'
import TrainingIndexvue from './components/templates/trainings/Index.vue'
import Training1Comp from './components/templates/trainings/Training1.vue'
import Training2Comp from './components/templates/trainings/Training2.vue'
import Training3Comp from './components/templates/trainings/Training3.vue'
import ActivationIndexvue from './components/templates/activations/Index.vue'
import Activation1Comp from './components/templates/activations/Activation1.vue'
import Activation2Comp from './components/templates/activations/Activation2.vue'
import Activation3Comp from './components/templates/activations/Activation3.vue'
import AboutComp from './components/About.vue'
import TrainingsComp from './components/Trainings.vue'
import ActivationsComp from './components/Activations.vue'
import ContactComp from './components/Contact.vue'
import SearchComp from './components/Search.vue'
export const routes = [
    {path:'/', component:IndexComp},
    {path:'/about', component:AboutComp},
    {path:'/trainings', component:TrainingsComp, children:[
        {path:'/trainings', component:TrainingIndexvue},
        {path:'/trainings/brand-life-obt', component:Training1Comp},
        {path:'/trainings/brand-life-classroom', component:Training2Comp},
        {path:'/trainings/brand-life-train-the-trainer', component:Training3Comp},
    ]},
    {path:'/activations', component:ActivationsComp, children:[
        {path:'/activations' , component:ActivationIndexvue},
        {path:'/activations/brand-life-activations', component:Activation1Comp},
        {path:'/activations/brand-life-engagement', component:Activation2Comp},
        {path:'/activations/brand-life-events', component:Activation3Comp},
    ]},
    {path:'/contact', component:ContactComp},
    {path:'/search', component:SearchComp, name:'search'},
    {path:'*', component:IndexComp},
]